html {
  overflow-y: scroll
}

body {
  margin: 0 auto;
  background-color: #e9ebef;
  font: .6rem/1.5 Microsoft Yahei,sans-serif;
  text-align: center;
  text-size-adjust: 100%;
  -moz-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
  -ms-text-size-adjust: 100%;
  -o-text-size-adjust: 100%
}

dd,div,dl,dt,form,h1,h2,h3,h4,h5,h6,img,input,li,ol,p,select,span,table,td,th,ul {
  margin: 0;
  padding: 0;
  font-weight: 400
}

iframe,img {
  border: none
}

li,ol,ul {
  list-style: none
}

table {
  border-collapse: collapse;
  border-spacing: 0
}

input {
  font-family: Microsoft Yahei,sans-serif
}

b,cite,em,font,i,strong,sub,sup {
  font-weight: 400;
  font-style: normal;
  font-size: 100%
}

a {
  text-decoration: none;
  -moz-tap-highlight-color: rgba(0,0,0,0);
  -webkit-tap-highlight-color: rgba(0,0,0,0);
  -ms-tap-highlight-color: rgba(0,0,0,0);
  -o-tap-highlight-color: rgba(0,0,0,0)
}

.clearfix {
  zoom: 1
}

body,html {
  height: 100%;
  width: 100%
}

input::-ms-clear {
  display: none
}

button,input[type=submit],input[type=reset],input[type=button],input[type=search] {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none
}

.h1,.h3,.h5 {font-weight: inherit;}

.img-100 {width: 100%;display: block}

/*中间件*/
.content {
  position: absolute;
  top: 0;
  bottom: 0;
  right: 0;
  width: 100%;
  overflow-y: scroll;
  overflow-x: hidden;
  -webkit-overflow-scrolling: touch;
  background-color: #F8F8F8;
  transition: all .3s ease;
}

.content > * {
  transform: translateZ(0);
}

.content > .blankback {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  height: 100%;
  background:rgba(0,0,0,.4);
  display: none;
  z-index: 9;
}


/*顶部*/
.content.hasheader {
  padding-top: 2.93rem;
}


header {
  position: fixed;
  top:0;
  right: 0;
  width: 100%;
  height: 2.93rem;
  border-bottom: 1px solid #ddd;
  z-index: 101;
  transition: all .3s ease; /* Safari */
  display: flex;
  flex-direction:row;
  flex-wrap: nowrap;
  justify-content:space-between;
  align-items:center;
  padding:0;
  box-sizing: border-box;
}
header > .logo {
  display: block;
  height: 2.30rem;
  margin: 0.23rem 0.76rem 0 0.67rem;
  color: #fff;
  font-size: 1rem;
  line-height: 2.30rem;
}
header > .logo span {
  font-weight: bold}
header > .logo em {
  background: #f09439;
  color: #000;
  padding: 0 0.35rem;
  border-radius: 0.25rem;
  font-size: 0.9375rem;
  font-weight: bold;
}
header > .title {
  display: block;
  text-align: left;
  font-size: 1.076923rem;
  font-weight: inherit;
  margin-bottom: 0;
  color:#FFF;
  flex-grow: 2;
  flex-basis:0;
}
header .icon {
  display: inline-block;
  width: 1.69rem;
  height: 1.69rem;
}
header .hs-search {
  height: 1.84rem;
  max-height: 1.84rem;
  flex-grow: 2;
  flex-basis:0;
  background:#fff;
  border-radius: 2.307692rem;
  line-height: 1.84rem;
  margin-right: 0.67rem;
  border:none;
  overflow: hidden;
  position: relative
}
header .hs-search > input[name="search"] {
  height: 1.84rem;
  max-height: 1.84rem;
  background:#fff;
  line-height: 1.84rem;
  width: 100%;
  text-align: left;
  border:none;
  display: inline-block;
  padding-left: 1.77rem;
  box-sizing: border-box;
  font-size: 0.92rem;
}
header .hs-search > em {
  display: inline-block;
  width: 1.23rem;
  height: 1.23rem;
  background: url('https://image.suning.cn/uimg/ZR/share_order/158782288287513670.jpg') no-repeat;
  background-size: 12.3rem;
  background-position: -11.07rem -1.23rem;
  top:0.30rem;
  left: 0.38rem;
  position: absolute;
}
header .hs-search > .search-button {
  position: absolute;
  right: 0.1125rem;
  top: 0.1125rem;
  color:#fff;
  border: none;
  width: 3.4375rem;
  height: 1.625rem;
  border-radius: 0.9375rem;
}


/* 显示或隐藏联想列表 */

.search .search-over {
  position: absolute;
  width: 100%;
  text-align: left;
  top: 2.93rem;
  padding-top: 0.3125rem;
  background: #fff;
  z-index: 99;
  opacity: 1;
}
.search .search-over.hd {
  z-index: -99;
  opacity: 0;
}
.search .search-over .list {
  display: flex;
  height: 2.8125rem;
  line-height: 2.8125rem;
  padding: 0 1.25rem;
  background: linear-gradient(180deg, #e1e1e1, transparent 67%, transparent 0) no-repeat bottom;
  background-size: 100% 1px;
  background-position: 0 0;
  align-items: center;
}
.search .search-over>.list>i {
  margin-right: 0.5rem;
  background:url('https://image.suning.cn/uimg/ZR/share_order/158782288287513670.jpg');
  background-size: 11.28rem 33.84rem;
  width: 1.128rem;
  height: 1.128rem;
  display: inline-block;
  background-position: -10.152rem -1.128rem
}
.search .search-over>.list>span {
  color: #333;
  font-size: 0.875rem
}

header .header-item {
  height: 2.93rem;
  display: flex;
  justify-content: center
}
header .header-item .icon,
header .header-item .words {
  display: inline-block;
  margin-right: 0.76rem;
  height: 1.69rem;
  margin-top:0.61rem;
}
header .header-item .icon {
  width: 1.69rem;
  justify-content: center
}
header .header-item .words {
  line-height: 1.69rem;
  color:#fff;
  vertical-align: top;
  font-size: 1rem;
}

header .icon.icon-search {
  background-position: 0 -3.38rem;
}
header .icon.icon-history {
  background-position: -1.69rem -3.38rem;
}
header .icon.icon-vip {
  background-position: -11.84rem -5.07rem;
}
header .icon.icon-back {
  background-position: -10.14rem -3.38rem;
  margin-left: 0.76rem;
}
header .icon.icon-skin {
  background-position: -1.69rem -10.14rem;
}
header .icon.icon-skin.active {
  background-position: 0 -10.14rem;
}
header .icon.icon-fav {
  background-position: -3.38rem -10.14rem;
}

.choose_skin {
  position: absolute;
  width: 100%;
  top: 0;
  height: 2.73rem;
  background: #fff;
  transition-duration: 400ms;
  z-index: 100
}
.choose_skin.active {top: 2.93rem}
.choose_skin .col {justify-content:center;display: flex}
.choose_skin em {
  display: flex;
  width: 1.25rem;
  height: 1.25rem;
  justify-content:center;
  align-items:center;
}
.choose_skin em > i {
  display: block;
  width: 0.4375rem;
  height: 0.4375rem;
  background:#fff
}


/*附头*/
.sub-header {
  position: fixed;
  top:2.93rem;
  height: 2.73rem;
  width: 100%;
  background:#fff;
  z-index: 99; /* Safari */
  display: flex;
  flex-direction:row;
  flex-wrap: nowrap;
  justify-content:space-between;
  align-items:center;
  padding:0;
  box-sizing: border-box;
  border-bottom: 1px solid #ddd;
}
.sub-header ~ .content,
.content.has-header {
  padding-top: 5.65rem;
}
.sub-header .sub-class {
  flex-grow: 2;
  flex-basis:0;
  height: 2.73rem;
  font-size: 14px;
  overflow: hidden;
}
.sub-header .sub-class ul {
  height: 2.73rem;
  width: auto;
  text-align: left;
  position:relative;
  white-space:nowrap;
}
.sub-header .sub-class ul li{
  white-space:nowrap;
  width: auto;
}
.sub-header .sub-class a {
  display: inline-block;
  padding:0 0.61rem;
  height: 2.73rem;
  flex-grow: 2;
  flex-basis:0;
  line-height: 2.73rem;
  font-size: 1.07rem;
  position: relative;
}
.sub-header .sub-class a > em {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  height: 0.23rem;
  display: none;
}
.sub-header .sub-class a.active > em {
  display: block;
}
.sub-header .open-class {
    display: block;
    width: 2.73rem;
    height: 2.73rem;
    line-height: 2.73rem;
    position: relative;
}
.sub-header .open-class .icon {
  display: block;
  width: 1.69rem;
  height: 1.69rem;
  margin-top:0.51rem;
}
.sub-header .open-class .icon-class {
  background-position: -1.69rem -1.69rem;
}
.sub-header .open-class .icon-class.active {
  background-position: -8.45rem -8.45rem;
}
.sub-header .open-class>span {
  position: absolute;
  left: -0.38rem;
  bottom: 1px;
  top: 0;
  width: .38rem;
  background-image: linear-gradient(to right, rgba(0,0,0,0) 0, rgba(0,0,0,0.1) 100%);
  background-repeat: repeat-x;
}

/*首页分类*/
.class {
  display: none;
  z-index: 9;
  background:#fff;
  position: fixed;
}
.class .col > span {
  display: block;
  line-height: 1.07rem;
  font-size: 1rem;
  margin-top:0.30rem;
}


