/*block*/
.block {
  background:#fff;
  margin-bottom: 0.77rem;
}
/*block-border*/
.block-border {
  border-top:1px solid #ddd;
  border-bottom: 1px solid #ddd;
}
.block-border:first-child {
  margin-top: 0px;
}
.block.block-no-margin {margin-bottom: 0}
/*catalog*/
.catalog {
  background:#fff;
  padding:0.38rem 0.38rem;
  border-top:1px solid #eee;
  border-bottom:1px solid #eee;
  margin-top: -1px;
  position: relative;
  box-sizing:border-box;
  color:#3a3a3a;
}
.catalog.no-border {border-top:none;border-bottom: none}
.catalog.margin-bottom {margin-bottom: .8rem;}
.catalog img {max-width: 100%;display: block;margin:0 auto;}
.catalog i.icon {
  width: 2.7rem;
  height: 2.7rem;
  display: block;
  background-size: 27.07rem 81.232rem;
}
.catalog i.icon-trash {
  background-position: -10.8rem -13.5rem;
}
.catalog i.icon-arrow-right {
  position: absolute;
  background-position: -2.375rem -0.03125rem;
  right: .4rem;
  top:50%;
  transform: translateY(-50%);
}
.catalog.active i.icon-arrow-right {
  background-position: -2.4375rem -0.875rem;
}
.catalog-divider {
  background:#fefefe;
  border-bottom: 1px solid #eee;
  font-size: 0;
  display: none;
}
.catalog-divider.active {
  display: block;
}

.b-title {
